.nav {
    padding: 40px 0;
    display: flex;
    &__row {
        display: flex;
        align-items: center; }
    &__logo {
        margin-right: 83px;
        font-size: 28px;
        &:hover {
            text-decoration: underline;
            cursor: pointer; } }
    &__list {
        margin: 0;
        padding: 0;
        display: flex;
        list-style-type: none; }
    &__item {
        margin-right: 30px;
        font-size: 18px;
        &:last-child {
            margin-right: 0; } }
    &__link {
        text-decoration: none;
        color: #000000;
        transition: 0.3s all;
        &:hover {
            color: #F72464; } }
    &__cart {
        margin-left: auto;
        &-link {
            position: relative;
            display: inline-block;
            top: 5px;
            padding: 5px; }
        &-number {
            position: absolute;
            top: -5px;
            right: -5px;
            width: 20px;
            height: 20px;

            border-radius: 50%;
            color: #fff;
            font-size: 12px;
            font-weight: bold;
            line-height: 20px;
            text-align: center;

            background-color: #F72464; } } }
